.App-header{
  background-color: #282c34;
  min-height: 85.8vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

footer{
    background-color: black;
    font-size: calc(10px + 2vmin);
    color: white;
    justify-content: center;
    align-items: center;
    min-height: 7vh;
}
.navbar {
  text-align:left;
  background-color: #14161A;
  font-size: calc(10px + 2vmin);
  color: white;
}

.navbar a {
	color:white;
	text-decoration:none;
	padding:10px;
	display:inline-block;
}

.navbar a.active {
	background-color:#282c34;
	color: lightblue;
}

.App-link {
  color: #61dafb;
}

